.App {
  font-family: sans-serif;
}

/* Header CSS Start */

.Navbar {
  display: flex; 
  border-bottom: 2px solid #F1F1F1;
  padding-left: 50px;
  padding-right: 50px;
}
.NavItemContainer{
  display: inline-flex;
  margin-left: 70px;
}
.NavItem{
  color: #464646;
  text-decoration: none;
  font-weight: bold;
  margin-left: 20px !important;
  margin-top: 18px;
}
.NavActive{
  border-bottom: 3px solid #3AB5E9;
}
.NavDropDown{
  color: #464646;
  text-decoration: none;
  font-weight: bold;
  margin-left: auto;
}

/* Header Css End */

/* Manage-Product Page CSS Start */

.MangProduct{
  padding-left: 175px;
  padding-right: 175px;
  padding-top: 55px;
}

.InpFieldLable{
  display: inline-table;
  font-size: 16px;
  font-weight: bold;
  color: #464646;
  margin-right: 50px;
  margin-bottom: 20px;
}
.InputField{
  margin: 10px 0;
}
small{
  color: #9b9b9b;
}
input[type=text],
textarea[type=text] {
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #969696;
  font-weight: bold;
  width: 300px;
  outline: none;
}
input[type=file] {
  padding: 0px;
  margin: 10px 0;
  border: 1px solid #969696;
  font-weight: bold;
  color: #464646;
  outline: none;
}
input[type=file]::-webkit-file-upload-button{
  padding: 10px;
  font-weight: bold;
  color: #464646;
  border: none;
  border-right: 1px solid #969696;
  outline: none;
}
.submitBtn{
  background: #3AB5E9;
  color: #ffffff;
  padding: 12px;
  outline: none;
  border: none;
  font-weight: bold;
  font-size: 16px;
}
.submitBtn:hover{
  box-shadow: 1px 5px 30px -4px rgba(0,0,0,0.24);
}
.submitBtn:active{
  box-shadow: none;
}
table{
  margin-top: 20px;
}
th{
  border-top: 1px solid #b6b6b6 !important;
  border-bottom: 1px solid #b6b6b6 !important;
  text-align: left;
  padding: 10px 50px 10px 0;
}
td {
  border-bottom: 1px solid #b6b6b6 !important;
  padding: 10px 0;
}
td span{
  margin-right: 50px;
}
td img {
  height: 40px;
  width: 40px;
  margin-right: 40px;
}
td button{
  background: transparent;
  outline: none;
  border: none;
  font-size: 15px;
  font-weight: bold;
  color: #3AB5E9;
}
/* Manage-Product Page CSS End */

/* Product Card CSS Start */
.ProdCard{
  display: inline-block;
  width: 200px;
  height: 250px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  margin: 30px;
}
.ProdCard:hover{
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}
.ProdCard img{
  width: 200px;
  height: 200px;
}

.cardInfo{
  display: inline-flex;
  width: 200px;
  margin-top: 10px;
}
.cardInfo .price{
  font-weight: bold;
  margin-left: auto;
  margin-right: 20px;
  font-size: 20px;
}
.cardInfo .name{
  margin-right: auto;
  margin-left: 20px;
  width: 75px;
}
/* Product Card CSS End */

/* Home page CSS start */

.HomePageView{
  padding: 0 174px;
}

/* Home page css End */

/* Responsive */
@media only screen and (max-width: 480px) {
  .MangProduct{
    padding-left: 45px !important;
    padding-right: 55px !important;
    padding-top: 25px !important;
  }
  .HomePageView{
    padding: 0 35px !important;
  }
}